import React from "react"
import Footer from "../components/footer/footer"
import Navbar from "../components/navbar/navbar"

import WishListJSON from "../jsonData/wishList.json"

import Meta from "../components/meta"

const data = JSON.parse(WishListJSON)

export default function WishList(props) {
  let arrItems = []

  for (let i = 0; i < Object.keys(data).length; i++) {
    arrItems.push(
      <div>
        <h4>{data[Object.keys(data)[i]]["title"]}</h4>
        <img
          src={data[Object.keys(data)[i]]["image"]}
          alt={data[Object.keys(data)[i]]["title"]}
        />
        <p>{data[Object.keys(data)[i]]["why"]}</p>
        <p>{data[Object.keys(data)[i]]["price"]}</p>
      </div>
    )
  }

  return (
    <div>
      <Meta
        title="Contact Me"
        desc="Contact Owen G. Bean with email owenbean400@gmail.com or with Linkedin"
      />
      <Navbar />
      <main className="wishlist-main">{arrItems}</main>
      <Footer />
    </div>
  )
}
